.container {
  padding: 10px;
}

.wrapper {
  background-color: #FFB600;
}

.container-aside-main {
  display: flex;
  flex-direction: column;
  justify-content: center;
}


@media (min-width: 768px) {

  .container-aside-main {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
  }
}

